.list-schedule {
  .stream-item {
    align-items: center;
    border-top: 1px solid #ccc;

    &.live {
      background-color: rgba(0, 0, 255, 0.1);
    }

    &.next-stream {
      border-top: 2px solid rgba(0, 0, 255, 0.5);
    }
  }

  .stream-toggles {
    user-select: none;

    .toggle-bookmark,
    .toggle-notification {
      display: inline-block;
      line-height: 1;
      padding: 0.2em;
      border-radius: 0.5em;

      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
        cursor: pointer;
      }
    }

    .toggle-bookmark.active {
      color: rgb(240, 160, 0);
    }

    .toggle-notification.active {
      color: rgb(240, 80, 80);
    }
  }

  .stream-vtuber {
    text-align: center;

    img {
      height: 4em;
      border-radius: 2.5em;
    }
  }

  .stream-info {
    img {
      height: 5em;
    }
  }
}
